import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { VhAlgorithm, VhQueryAffiliater } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';

@Component({
  selector: 'app-manage-shop-payment',
  templateUrl: './manage-shop-payment.component.html',
  styleUrls: ['./manage-shop-payment.component.scss']
})
export class ManageShopPaymentComponent implements OnInit {

  public date: Date[] = [new Date(), new Date()];
  pageIndex: number = 0;
  pageSize: number = 20;
  lowValue: number = 0;
  highValue: number = 20;
  pageSizeOption: any = [20, 50, 100, 200, 500];
  data: any = [];

  loading: boolean = false;
  isVisibleExtendUse = false;
  key_code = '';
  customer: any;

  constructor(
    private route: ActivatedRoute,
    private vhQueryAffiliater: VhQueryAffiliater,
    private vhComponent: VhComponent,
    private cdRef: ChangeDetectorRef,
    public vhAlgorithm: VhAlgorithm
  ) {
  }

  ngOnInit(): void {
  }

  heightScroll: string;
  ngAfterViewChecked() {
    if (document.querySelector(".height-layout") && document.querySelector(".height-header") && document.querySelector(".ant-table-thead") && document.querySelector(".ant-table-pagination")) {
      this.heightScroll = document.querySelector(".height-layout").clientHeight -
        document.querySelector(".height-header").clientHeight -
        document.querySelector(".ant-table-thead").clientHeight - 100 + "px";
    }
    this.cdRef.detectChanges();
  }
  getData() {
    this.loading = true;
    this.vhQueryAffiliater.getPayments_byShop().then((response: any) => {
      this.data = response;
      console.log(this.data);
      this.loading = false;
    })
  }

  copyValue(value) {
    this.vhComponent.copyValue(value)
  }

}
